<template>
  <div id="hello">
    <div id="generated">
      <h1> {{ `${sentence.start} ${sentence.subject} ${sentence.verb} ${sentence.adverb} ${sentence.prep}` }}</h1>
    </div>
    <button
      id="genButton"
      @click="post"
    >
      GENERATE
    </button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Sentence',
  data: () => ({
    sentence: ''
  }),
  mounted() {
    axios
      .get('/api/sentence/1')
      .then(response => {
        // const sent = `${this.start} ${this.subject} ${this.verb} ${this.adverb} ${this.prep}`
      this.sentence = response.data;
      // sentString = response.data;
      })
      .catch(console.error)
  },
  methods: {
    post: function () {
      axios
        .post('/api/newSentence')
        .then(response => {
        this.sentence = response.data;
        this.$emit("update-sentence", this.sentence);
        })
        .catch(console.error)
    }
  }
};

</script>

